.footer {
    position: relative; }
.map {
    position: absolute;
    left: 0;
    top: 0;
    width: 50%; }
.contacts {
    flex-basis: 50%;
    padding-top: 100px;
    padding-left: 200px;
    &__title {
        @include bold;
        font-size: 22px;
        line-height: 1.4;
        margin-bottom: 100px; }
    &-block {
        display: flex;
        justify-content: flex-end; }
    &-list {
        padding: 0;
        list-style-type: none;
        &__icon {
            width: 20px;
            height: 20px;
            text-align: center;
            line-height: 20px;
            margin-right: 23px; }
        &__item {
            display: flex;
            align-items: flex-start;
            margin-bottom: 39px; }
        &__text {
            max-width: 160px;
            span {
                line-height: 26px;
                color: #000000;
                @include bold; }
            &_link {
                line-height: 26px;
                text-decoration: none;
                color: #000000;
                @include bold; } } } }
@media (max-width: 768px) {
    .footer {
        position: static;
        display: flex;
        flex-direction: column-reverse; }
    .map {
        position: static;
        width: 100%; }
    .contacts {
        flex-basis: 100%;
        padding: 0;
        &__title {
            text-align: center; } } }
