.offer {
    background: url('../img/offer-bg.jpg') no-repeat;
    background-position: center right;
    background-size: contain;
    position: relative;
    &::before {
        content: '';
        width: 50%;
        height: 100%;
        position: absolute;
        background-color: #ffffff;
        top: 0;
        left: 0; }
    &-block {
        width: 50%;
        padding-right: 90px;
        position: relative; }
    &__title {
        max-width: 360px;
        line-height: 35px;
        margin-bottom: 24px;
        text-align: left; }
    &__subtitle {
        display: block;
        max-width: 250px;
        margin-bottom: 30px; }
    &__input {
        margin-bottom: 30px;
        width: 240px;
        &:first-child {
            margin-right: 10px; } }
    &__button {
        width: 100%;
        min-height: 70px;
        color: $text-color;
        @include bold;
        font-size: 16px;
        line-height: 18px; }
    &-text {
        display: flex;
        justify-content: space-between;
        margin-top: 30px;
        &__descr {
            color: $text-color;
            font-size: 15px;
            line-height: 24px;
            max-width: 225px; }
        &__call {
            color: $text-color;
            font-size: 15px;
            line-height: 24px; } }
    &__phone {
        display: block;
        color: $text-color;
        @include bold;
        font-size: 15px;
        line-height: 24px;
        text-decoration: none; } }
@media (max-width: 1200px) {
    .offer {
        background-size: cover;
        &__input {
            width: 100%;
            margin-bottom: 10px; }
        &__button {
            font-size: 13px; } } }
@media (max-width: 768px) {
    .offer {
        background: #ffffff;
        &-block {
            width: 100%;
            padding-right: 0; } } }


