* {
    box-sizing: border-box; }
body {
    @include reg; }
img {
    max-width: 100%; }
.container {
    width: 1170px;
    margin: auto; }
@media (max-width: 1200px) {
    .container {
        width: 760px; } }
@media (max-width: 768px) {
    .container {
        max-width: 580px;
        width: 90%; }
    .section {
        padding-top: 60px;
        padding-bottom: 60px; } }
@media (max-width: 567px) {
    .container {
        max-width: 450px;
        width: 90%; } }
.button {
    border-radius: 10px;
    background-color: $main-color;
    font-size: 13px;
    border: none;
    padding: 18px 30px;
    &:hover {
        background-color: lighten($main-color, 10%);
        transition: 0.2s;
        cursor: pointer; } }
.section {
    padding: 90px 0;
    &-title {
        color: #333333;
        font-size: 24px;
        max-width: 500px;
        @include bold;
        text-align: center; }
    &-subtitle {
        color: #333333;
        font-size: 16px;
        @include reg; } }
.input {
    border: 1px solid $border-input;
    padding: 20px 26px;
    @include reg;
    font-size: 15px;
    border-radius: 10px; }
.error {
    border: 1px solid red; }
